/* Set the background color or image for the entire app */
body {
  background-color: #97c6cc; /* Light blue background */
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
font-size: 12px;
}

/* Main container for the app */
.app-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 10px;
  max-width: 600px;
  margin: auto;
}

/* Style for the upload section */
input[type="file"] {
  margin: 20px;
  padding: 10px;
  background-color: #4bbfa2;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

input[type="file"]:hover {
  background-color: #237561;
}

/* Image preview styling */
img {
  margin-top: 20px;
  width: 300px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Button styling */
button {
  background-color: #bf4b6a;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 20px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #80263e;
}

/* Wheel container and spin button */
.wheel-container {
  position: relative;
  width: 300px;
  height: 300px;
  margin: 20px auto;
}

.wheel {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: transform 3s ease-out;
}

.wheel.spin {
  animation: spin 3s ease-out forwards;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(1440deg); /* 4 full spins (360 * 4) */
  }
}

.wheel-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffcc5c;
  color: #333;
  width: 50px; /* Fixed width */
  height: 50px; /* Fixed height */
  font-size: 10px; /* Adjust font size */
  border: none;
  border-radius: 50%; /* Make it circular */
  cursor: pointer;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease;
  z-index: 1;
}

.wheel-button:hover {
  background-color: #ffaa00;
}

/* Maintain circular shape even during spinning */
.wheel-button:disabled {
  background-color: #ffcc5c;
  cursor: not-allowed;
}

/* Gift message styling */
.result-message {
  margin-top: 30px;
  font-size: 24px;
  color: #4bbfa2;
  text-align: center;
}

.congratulations-message {
  margin-top: 10px;
  font-size: 20px;
  color: #80263e;
  font-weight: bold;
}
